* {
    box-sizing: border-box;
  }
  
  body {
    margin: 0;
    padding: 0;
    font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
    background-color: #eff3f7;
    height: 100%;
  }
  a {
    text-decoration: none;
    color: #2c2c2c;
  }
  
  ul {
    list-style: none;
    display: flex;
    padding: 0;
    margin: 0;
  }
  
  html,
  body,
  #root,
  .container {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
  }
  h2,
  h3,
  h5,
  p {
    margin: 0;
    padding: 0;
  }
  

