.wrap{
    background-color: #3f3f3f;
    padding: 100px 0;
    display: flex;
    height: 100%;
    justify-content: center;
    width: 100%;
}
.wrap > div {
    background-color: #fff;
    border-radius: 5px;
    display: grid;
    gap: 20px;
    grid-template-columns: 1fr 2fr;
    padding: 40px;
    width: 60%;
}

.wrap > div img{width:100%}

