.card_wrap {
    background-color: grey;
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 100px 50px;
    padding: 50px;
    
}

.card_wrap>div {
    border-radius: 12px;
    display: flex;
    gap: 50px;
    height: 400px;
    background-color: white;
    padding: 40px
}